import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider, Spin } from 'antd';
import { BtnSave, BtnExport, BtnExportPdf, BtnAddNew, Btn } from 'components/common/button';
import {
  exportExcelCertificateAssignment,
  exportCertificateAssignment,
} from 'services/certificateAssignment';
import FormInfoStudentForCert from './FormInfoStudentForCert';
import TableCertificateSelected from './TableCertificateSelected';
import TableCertificateNotSelected from './TableCertificateNotSelected';

import './style.scss';

class ReviewForCertificationPage extends Component {
  state = {
    paramsSearch: {},
    listStudentNotSelect: [],
    studentNotSelect: [],
    removeStudent: [],
    listStudentSelect: [],
    removeStudentTableSelect: [],
    saveDataStudent: false,
    listStudentAdd: [],
    loadingPage: false,
    dataTableStudentNotSelect: [],
  };
  setLoadingSave = value => {
    this.setState({
      loadingPage: value,
    });
  };

  setDataTable = (value, action) => {
    console.log('setDataTable', value);
    if (action === 'remove') {
      this.setState({
        removeStudent: [],
        dataTableStudentNotSelect: Object.values(value),
      });
    } else {
      this.setState({
        dataTableStudentNotSelect: Object.values(value),
        listStudentAdd: [],
      });
    }
  };

  dataForm = value => {
    this.setState({ paramsSearch: value });
  };

  studentSelect = value => {
    this.setState({
      listStudentSelect: value,
    });
  };

  studentNotSelect = value => {
    this.setState({
      listStudentNotSelect: value,
    });
  };

  addStudent = () => {
    const { listStudentNotSelect } = this.state;
    this.setState({
      listStudentNotSelect: [],
      studentNotSelect: listStudentNotSelect,
      removeStudent: listStudentNotSelect,
      listStudentAdd: [],
      removeStudentTableSelect: [],
    });
  };

  deleteStudent = () => {
    const { listStudentSelect } = this.state;
    this.setState({
      listStudentSelect: [],
      removeStudentTableSelect: listStudentSelect,
      listStudentAdd: listStudentSelect,
      removeStudent: [],
      studentNotSelect: [],
    });
  };

  saveListStudent = () => {
    this.setState({ saveDataStudent: true });
  };

  exportExcel = async () => {
    const { paramsSearch } = this.state;
    this.setState({
      loadingPage: true,
    });
    try {
      await exportExcelCertificateAssignment(paramsSearch);
    } finally {
      this.setState({
        loadingPage: false,
      });
    }
  };
  exportPdf = async () => {
    const { paramsSearch } = this.state;
    this.setState({
      loadingPage: true,
    });
    try {
      await exportCertificateAssignment(paramsSearch);
    } finally {
      this.setState({
        loadingPage: false,
      });
    }
  };

  render() {
    const {
      paramsSearch,
      listStudentNotSelect,
      studentNotSelect,
      removeStudent,
      listStudentSelect,
      removeStudentTableSelect,
      saveDataStudent,
      listStudentAdd,
      loadingPage,
      dataTableStudentNotSelect,
    } = this.state;
    return (
      <Card title="Xét cấp chứng chỉ">
        <Spin spinning={loadingPage} size="large">
          <Row>
            <Col span={24}>
              <FormInfoStudentForCert
                dataStudent={dataTableStudentNotSelect}
                dataForm={this.dataForm}
              />
            </Col>
          </Row>
          <Divider>Danh sách sinh viên chưa chọn</Divider>
          <Row>
            <Col span={24}>
              <TableCertificateNotSelected
                paramsSearch={paramsSearch}
                studentNotSelect={this.studentNotSelect}
                removeStudent={removeStudent}
                dataTable={this.setDataTable}
                listStudentAdd={listStudentAdd}
                listStudentNotSelect={listStudentNotSelect}
              />
            </Col>
          </Row>
          <Divider>Danh sách sinh viên đã chọn</Divider>
          <Row>
            <Col span={12}>
              <BtnAddNew disabled={listStudentNotSelect.length === 0} onClick={this.addStudent} />
              <Btn
                style={{ marginLeft: 10 }}
                disabled={listStudentSelect.length === 0}
                title="Xóa"
                onClick={this.deleteStudent}
              />
            </Col>
            <Col span={12} style={{ textAlign: 'end' }}>
              <BtnSave onClick={this.saveListStudent} />
              <BtnExport
                style={{ marginLeft: 10, marginRight: 10 }}
                onClick={() => this.exportExcel()}
              />
              <BtnExportPdf onClick={() => this.exportPdf()} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableCertificateSelected
                loading={this.setLoadingSave}
                paramsSearch={paramsSearch}
                saveDataStudent={saveDataStudent}
                studentSelect={this.studentSelect}
                listStudentAdd={studentNotSelect}
                removeStudent={removeStudentTableSelect}
                listStudentSelect={listStudentSelect}
                saveData={() => this.setState({ saveDataStudent: false })}
              />
            </Col>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReviewForCertificationPage />
  </Page>
);



