import React, { Component } from 'react';
import to from 'await-to-js';
import { Table } from 'antd';
import { getCertificateAssignment } from 'services/certificateAssignment';

class TableCertificateNotChecked extends Component {
  state = {
    selectedRowKeys: [],
    dataTable: {},
  };

  componentWillReceiveProps(nexProps) {
    const { dataTable } = this.state;
    const dataFormat = dataTable;
    if (nexProps.paramsSearch !== this.props.paramsSearch)
      this.getTableStudentNotChecked(nexProps.paramsSearch);
    if (nexProps.removeStudent.length > 0) {
      nexProps.removeStudent.forEach(item => {
        delete dataFormat[item.studentId];
      });
      this.setState({
        dataTable: dataFormat,
      });
      this.props.dataTable(dataTable, 'remove');
    } else if (nexProps.listStudentAdd.length > 0) {
      nexProps.listStudentAdd.forEach(item => {
        dataFormat[item.studentId] = item;
      });
      this.props.dataTable(dataTable, 'add');
      this.setState({
        dataTable: dataFormat,
      });
    }
    if (nexProps.listStudentNotSelect.length === 0) {
      this.setState({
        selectedRowKeys: [],
      });
    }
  }
  onSelectChange = (selectedRowKeys, rowSelect) => {
    const dataSelect = selectedRowKeys;
    this.setState({ selectedRowKeys: dataSelect });
    this.props.studentNotSelect(rowSelect);
  };

  getTableStudentNotChecked = async paramsSearch => {
    this.setState({ loading: true });
    const [error, response] = await to(getCertificateAssignment(paramsSearch));
    this.setState({
      loading: false,
    });
    if (error) return;
    const dataTable = {};
    response.data.items.forEach(item => {
      dataTable[item.studentId] = item;
    });
    this.props.dataTable(dataTable);
    this.setState({
      dataTable,
    });
  };
  render() {
    const { dataTable, loading, selectedRowKeys } = this.state;
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'TBC chứng chỉ',
        dataIndex: 'certificateType',
        key: 'certificateType',
      },
      {
        title: 'Xếp loại',
        dataIndex: 'certificateClassification',
        key: 'certificateClassification',
      },
      {
        title: 'Lần xét',
        dataIndex: 'estimateRound',
        key: 'estimateRound',
      },
      {
        title: 'Lý do không đủ điều kiện cấp',
        dataIndex: 'reason',
        key: 'reason',
      },
    ];
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <Table
        bordered
        rowSelection={rowSelection}
        loading={loading}
        dataSource={Object.values(dataTable)}
        columns={columns}
        rowKey="studentId"
      />
    );
  }
}

export default TableCertificateNotChecked;



