import React, { Component } from 'react';
import { Row, Col, Form, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnClear, BtnSearch } from 'components/common/button';

import { SelectAjax, InputNumberScore10, InputOnlyNumber } from 'components/common';
import { getCertificateTypes } from 'services/certificateType';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudent extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      updateQueryParams(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form onSubmit={this.handleSearch} layout="vertical" className="form-inline-label">
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={5}>
            <Form.Item label="Chứng chỉ">
              {getFieldDecorator('certificateTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn loại chứng chỉ',
                  },
                ],
              })(
                <SelectAjax
                  // mode="multiple"
                  // setFieldsValue={setFieldsValue}
                  service={getCertificateTypes}
                  // params={{}}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Lần xét">
              {getFieldDecorator(
                'estimateRound',
                {}
              )(<InputOnlyNumber style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Điểm xét lại">
              {getFieldDecorator('grade', {})(<InputNumberScore10 style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={9}>
            <Form.Item label="TBC chứng chỉ đạt">
              {getFieldDecorator('verifyAll', {
                initialValue: true,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn ',
                  },
                ],
                valuePropName: 'checked',
              })(<Checkbox style={{ marginTop: 10, marginLeft: 10 }} />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator(
                'data',
                {}
              )(<div>Tổng số sinh viên: {this.props.dataStudent.length}</div>)}
            </Form.Item>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
        {/* <Row gutter={24}>
         
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('totalStudent', {})(<div>Tổng số sinh viên: 1</div>)}
            </Form.Item>
          </Col>
        </Row> */}
      </Form>
    );
  }
}

export default FormInfoStudent;



