import { request, requestDownload } from 'utils';
// import qs from 'qs';

const prefix = '/api/education';

export const getCertificateAssignment = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  certificateTypeId,
  estimateRound,
  grade,
  verifyAll,
} = {}) =>
  request({
    prefix,
    url: '/CertificateAssignment',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      certificateTypeId,
      estimateRound,
      grade,
      verifyAll,
      isPagingEnabled: false,
    },
  });

export const getStudentHasCertificateAssignment = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  certificateTypeId,
  estimateRound,
} = {}) =>
  request({
    prefix,
    url: '/CertificateAssignment/StudentHasCertificateAssignment',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      certificateTypeId,
      estimateRound,

      isPagingEnabled: false,
    },
  });

export const saveCertificateAssignment = dataStudent =>
  request({
    prefix,
    url: '/CertificateAssignment',
    method: 'POST',
    data: dataStudent,
  });

export const exportExcelCertificateAssignment = paramsSearch =>
  requestDownload({
    defaultFileName: `ExportExcelCertificateAssignment.xlsx`,
    prefix,
    url: '/CertificateAssignment/ExportExcelCertificateAssignment',
    method: 'POST',
    data: paramsSearch,
  });

export const exportCertificateAssignment = paramsSearch =>
  requestDownload({
    defaultFileName: `ExportCertificateAssignment.pdf`,
    prefix,
    url: '/CertificateAssignment/ExportCertificateAssignment',
    method: 'POST',
    data: paramsSearch,
  });



