import React, { Component } from 'react';
import to from 'await-to-js';
import { Table, message } from 'antd';
import {
  getStudentHasCertificateAssignment,
  saveCertificateAssignment,
} from 'services/certificateAssignment';

class TableCertificateNotChecked extends Component {
  state = {
    selectedRowKeys: [],
    dataTable: {},
  };

  componentWillReceiveProps(nexProps) {
    const { dataTable } = this.state;
    const dataFormat = dataTable;
    if (nexProps.saveDataStudent !== this.props.saveDataStudent) {
      if (nexProps.saveDataStudent) {
        this.saveStudent();
      }
    } else {
      if (nexProps.paramsSearch !== this.props.paramsSearch) {
        this.getTableStudentNotChecked(nexProps.paramsSearch);
      }
      if (nexProps.listStudentAdd.length > 0) {
        nexProps.listStudentAdd.forEach(item => {
          dataFormat[item.studentId] = item;
        });
        this.setState({
          dataTable: dataFormat,
        });
      } else if (nexProps.removeStudent.length > 0) {
        nexProps.removeStudent.forEach(item => {
          delete dataFormat[item.studentId];
        });
        this.setState({
          dataTable: dataFormat,
        });
      }
      if (nexProps.listStudentSelect.length === 0) {
        this.setState({
          selectedRowKeys: [],
        });
      }
    }
  }
  onSelectChange = (selectedRowKeys, rowSelect) => {
    this.setState({ selectedRowKeys });
    this.props.studentSelect(rowSelect);
  };

  getTableStudentNotChecked = async paramsSearch => {
    this.setState({ loading: true });
    const [error, response] = await to(getStudentHasCertificateAssignment(paramsSearch));
    this.setState({ loading: false });
    if (error) return;
    const dataTable = {};
    response.data.items.forEach(item => {
      dataTable[item.studentId] = item;
    });
    this.setState({
      dataTable,
    });
  };

  saveStudent = async () => {
    const { dataTable } = this.state;
    const { paramsSearch } = this.props;
    const dataSave = Object.values(dataTable).map(item => {
      const { studentId, certificateClassificationId, grade } = item;
      const itemFormat = {
        studentId,
        certificateClassificationId,
        grade,
      };
      return itemFormat;
    });

    const dataUpdate = {
      certificateTypeId: paramsSearch.certificateTypeId,
      estimateRound: Object.values(dataTable)[0].estimateRound,
      details: dataSave,
    };
    console.log('dataUpdate', dataUpdate);

    this.props.loading(true);
    const [error, response] = await to(saveCertificateAssignment(dataUpdate));
    this.props.saveData();
    this.props.loading(false);
    if (error) return;
    if (response) {
      message.success('Lưu thông tin thành công');
    }
  };
  render() {
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'TBC chứng chỉ',
        dataIndex: 'certificateType',
        key: 'certificateType',
      },
      {
        title: 'Xếp loại',
        dataIndex: 'certificateClassification',
        key: 'certificateClassification',
      },
      {
        title: 'Lần xét',
        dataIndex: 'estimateRound',
        key: 'estimateRound',
      },
      {
        title: 'Lý do không đủ điều kiện cấp',
        dataIndex: 'reason',
        key: 'reason',
      },
    ];
    const { dataTable, loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <Table
        rowSelection={rowSelection}
        loading={loading}
        bordered
        dataSource={Object.values(dataTable)}
        columns={columns}
        rowKey="studentId"
      />
    );
  }
}

export default TableCertificateNotChecked;



